import React, { Component } from "react";
import Slider from "react-slick";
import styled from "styled-components";

const Item = styled.div`
  //background-color: #634e95;
  height: 42px;
  line-height: 42px;
  text-align: center;
  span {
    font-size: 11px;
    font-weight: 500;
    color: #818181;
  }
`;

const Index = styled.p`
  display: inline;
  color: #e67404;
  font-size: 11px;
  font-weight: 500;
`;

const Title = styled.p`
  width: 180px;
  //background-color: #a7c7e3;
  display: inline-block;
  max-width: 180px;
  color: black;
  vertical-align: middle;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 13px;
`;

const testitem = [
  { index: "1", name: "  MBC   ", count: 102 },
  { index: "2", name: ", ' 4'    ", count: 88 },
  { index: "3", name: ",    ", count: 86 },
  {
    index: "4",
    name: " 3, ' '   ",
    count: 76,
  },
  { index: "5", name: "  ,  ", count: 56 },
  {
    index: "6",
    name: " ,      ",
    count: 52,
  },
  {
    index: "7",
    name: ",     ",
    count: 34,
  },
  { index: "8", name: ` D-7, "  "`, count: 21 },
  { index: "9", name: "' '   ", count: 19 },
  { index: "10", name: ",    ", count: 8 },
];

const NowRank = () => {
  const settings = {
    infinite: true,
    slidesToShow: 1,
    slidesToScroll: 1,
    autoplay: true,
    speed: 500,
    autoplaySpeed: 3000,
    vertical: true,
    arrows: false, //Previous  
  };

  return (
    <Slider {...settings}>
      {testitem.map((item) => (
        <Item>
          <Index>{item.index + ". "}</Index>
          <Title>{item.name}</Title>
          <span>{item.count}</span>
        </Item>
      ))}
    </Slider>
  );
};

export default NowRank;
